import uniGrid from '@/components/uni-grid/uni-grid.vue'
import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue'
export default {
	components: {
		uniGrid,
		uniGridItem
	},
	data() {
		return {
			title: '买卖',
			lists: [
				{id: '1', name: '销售', icon: 'sale', color: '#ed3f14' },
				{id: '2', name: '采购', icon: 'purchase-fill', color: '#2d8cf0' },
				{id: '3', name: '客户&供应商', icon: 'customer-fill', color: '#ff9900' },
				{id: '4', name: '产品', icon: 'product-fill', color: '#2d8cf0' },
				{id: '5', name: '收款单', icon: 'receipt', color: '#ed3f14' },
				{id: '6', name: '付款单', icon: 'payment', color: '#ff9900' },
				{id: '7', name: '费用单', icon: 'cost', color: '#19be6b' },
				{id: '8', name: '退货单', icon: 'return-order', color: '#19be6b' }
			]
		}
	},
	onLoad() {
	},
	methods: {
		handleRefreshPage() {
			console.log("refreshpage")
		},
		handleGridChange(val) {
			switch(val.id) {
				case '1':
					uni.navigateTo({
						url: './sale/sale'
					})
					break
				case '2':
					uni.navigateTo({
						url: './purchase/purchase'
					})
					break
				case '3':
					uni.navigateTo({
						url: './current_unit/current_unit'
					})
					break
				case '4':
					uni.navigateTo({
						url: './product/product'
					})
					break
				case '5':
					uni.navigateTo({
						url: './receipt/receipt'
					})
					break
				case '6':
					uni.navigateTo({
						url: './payment/payment'
					})
					break
				case '7':
					uni.navigateTo({
						url: './cost/cost'
					})
					break
				case '8':
					uni.navigateTo({
						url: './return-order/return-order'
					})
					break
			}
		}
	}
}
